<template>
    <section class="layout">

        <Form v-if="!isRegistrationSuccessful && !isAuthorized">
            <h1 class="title">Регистрация</h1>

            <div class="item">
                <MDInput label="Почта" ref="login" @input="onInput"></MDInput>
            </div>

            <div class="item">
                <MDPasswordInput label="Пароль" ref="password" @input="onInput"></MDPasswordInput>
            </div>

            <div class="item">
                <MDPasswordInput label="Повторите Пароль" ref="confirmPassword" @input="onInput"></MDPasswordInput>
            </div>

            <div class="item">
                <MDInput label="Реквизиты" ref="requisites" @input="onInput"></MDInput>
            </div>

            <div class="item">
                <router-link :to="{ name: 'login'}">войти в аккаунт</router-link>
            </div>

            <div class="item" v-if="err.length !== 0">
                <p class="error">{{err}}</p>
            </div>

            <div class="item bottom-item">
                <button v-on:click="register" v-if="!isRegistering">Регистрация</button>
                <LoadingSpinner v-if="isRegistering"/>
            </div>
        </Form>

        <Form v-if="isRegistrationSuccessful">
            <h1 class="title">Ваша заявка была успешна принята. Ожидайте ее подтверждение.</h1>
            <div class="item">
                <router-link :to="{ name: 'login'}">войти в аккаунт</router-link>
            </div>
        </Form>

        <Form v-if="isAuthorized">
            <h1 class="title">Вы уже вошли в аккаунт</h1>
        </Form>

    </section>
</template>

<script>
    import LoadingSpinner from "./components/LoadingSpinner"
    import MDInput from "./components/MD/MDInput";
    import MDPasswordInput from "./components/MD/MDPasswordInput";
    import Form from "./components/Form";

    import jToken from "../common/jToken"
    import {AuthApi} from "../api/auth";

    const emailValidator = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    export default {
        name: "Register",

        components: {Form, MDInput, LoadingSpinner, MDPasswordInput},

        data: () => {
            return {
                isAuthorized: false,
                isRegistrationSuccessful: false,
                isRegistering: false,

                err: "",

                incorrectData: [],
            }
        },

        methods: {
            onInput() {
                this.$refs.login.isValid = !this.$refs.login.isEmpty;
                if (this.$refs.login.isValid) {
                    this.$refs.login.isValid = emailValidator.test(String(this.$refs.login.data).toLowerCase())
                }

                this.$refs.password.isValid = !this.$refs.password.isEmpty;
                this.$refs.confirmPassword.isValid = !this.$refs.confirmPassword.isEmpty;
                this.$refs.requisites.isValid = !this.$refs.requisites.isEmpty;

                if (this.$refs.password.data === this.$refs.confirmPassword.data && this.$refs.password.isValid) {
                    this.err = '';

                    if (!this.$refs.login.isValid) {
                        return;
                    }

                    let res = this.incorrectData.find((item) => item.login === this.$refs.login.data);
                    if (res) {
                        this.err = 'Почта уже зарегистрирована.';
                        this.$refs.login.isValid = false;
                    } else if (this.$refs.password.data.length < 7) {
                        this.err = 'Пароль должен состоят минимум из 7 знаков.';
                        this.$refs.password.isValid = false;
                    }

                } else if (this.$refs.password.data!== this.$refs.confirmPassword.data && this.$refs.password.isValid) {
                    this.err = 'Пароли должны совпадать.';
                    this.$refs.password.isValid = false;
                    this.$refs.confirmPassword.isValid = false;
                }
            },

            register() {
                if (this.isFormValid) {
                    this.isRegistering = true;

                    let login = this.$refs.login.data;
                    let password = this.$refs.password.data;
                    let requisites = this.$refs.requisites.data;

                    AuthApi.register(login, password, requisites)
                        .then((res) => {
                            this.isRegistering = false;
                            if (res.ok) {
                                this.isRegistrationSuccessful = true;
                            } else {
                                console.error({res});
                                this.err = "Произошла ошибка на сервере. Попробуйте позже.";
                            }
                        })
                        .catch(err => {
                            this.isRegistering = false;
                            console.error({err});
                            this.err = "Произошла ошибка на сервере. Попробуйте позже.";
                        })
                }
            },
        },

        computed: {
            isFormValid: function () {
                if (!this.isAuthorized) {
                    return this.$refs.login.isValid && this.$refs.password.isValid
                        && this.$refs.confirmPassword.isValid && this.$refs.requisites.isValid
                } else {
                    return false
                }
            }
        },

        mounted() {
            this.isAuthorized = jToken.has();

            this.onInput();
        },
    }
</script>

<style scoped lang="sass">
    .layout
        width: 90%
        max-width: 500px
        margin: auto
        font-family: Roboto, sans-serif

</style>